  @Input() rectifyReferenceH: HTMLElement;
  // çºµåçº æ­£çåç§
  @Input() rectifyReferenceV: HTMLElement;
  // æ¯å¦èªå¨çº æ­£ä½ç½®
  @Input() autoRectify = true;
  // è®¡ç®æ¹åççæ­£placement
