  // éæ°è®¡ç®åçplacement
  rectifyPlacement = 'bottom-right';

  /* æé®ä¿¡æ¯ */
  @Input() data: Button[];

  /* æ¾ç¤ºçæé®æ°é  é»è®¤ä¸º2 */
