  /* æé®å±ç¤ºä½ç½® */
  private _placement = "bottom";
  @Input()
  set placement(value) {
    if (!value) {
      return;
    }
    if (value !== this._placement) {
      this.realPlacement = this.getRealPlacement(value);
      this._placement = value;
    }
  }
  get placement() {
    return this._placement;
  }

  /* menu size */
  /**
   *2021.06.12 åºå¼ï¼å ä¸ºä½¿ç¨èä¸ç¥éå¨ä¸­è±ææåµä¸ï¼ä¸æé¢æ¿çå®½åº¦åé«åº¦
   */
  @Input() width = 120;
  @Input() height = 250;
  private _menuWidth = 0;
  private _menuHeight = 0;

  /* ä¸ææé® è§¦åäºä»¶ */
  @Output() changeState = new EventEmitter<any>();

  /* ä¸æèåé¡¹ç¹å»äºä»¶ */
  @Output() change = new EventEmitter<any>();
  @Output() click = new EventEmitter<any>();

  /* è§¦åä¸ææé®é¢æ¿åºåä¹å¤çäºä»¶ */
  @Output() clickMenuOut = new EventEmitter<any>();

  /**
   * 2021.06.12 åºå¼ï¼å ä¸ºå½autoSizeä¸ºtrueçæ¶åï¼æé®æ¾ç¤ºä¸åºæ¥ã
   */
  @Input() autoSize = false;


  /* ä¸æé¢æ¿æ¾ç¤º */
  show = false;
  /* æ¾ç¤ºåºæ¥çæé®ç» */
